import { useEffect } from "react";


function Mypage() {

    useEffect(()=>{
        window.scrollTo(0,0)  //  
    },[])

    return (
        <div style={{width:'360px', height: '750px', margin:'0 auto'}} className="mb-5">
            <h4 className="mt-5 mb-5">MyPage</h4>            
            <p><b style={{color:'palevioletred'}}>{localStorage.getItem('springStar_nick')}</b>   ...</p>
            <img className="mypage"></img>
            <p>    ...</p>
        </div>
    )
}


export default Mypage;