import React, { lazy, Suspense, useEffect, useState } from 'react';
import './App.css';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import data from './data'
import axios from 'axios';
import { Routes, Route, Link, useNavigate, Outlet, Await } from 'react-router-dom';
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { useQuery } from 'react-query'
import  Spinner  from './component/Spinner';

import Cart from './routes/Cart'
import Member from './routes/Member'
import Login from './routes/Login'
import Board from './routes/Board'
import Man from './routes/Man'
import Woman from './routes/Woman'
import Living from './routes/Living'
import Detail from './routes/Detail'
import Create from './routes/Create';
import Mypage from './routes/Mypage'
import NotAccess from './routes/NotAccess';


// const Man  = lazy(()=> import('./routes/Man'));
// const Woman  = lazy(()=> import('./routes/Woman'));
// const Living  = lazy(()=> import('./routes/Living'));
// const Detail  = lazy(()=> import('./routes/Detail'));


function App() {
    let navigate = useNavigate()
    let [shoes, setShoes] = useState(data);
    let [rItem, setRItem] = useState(false)
    let [fade, setFade] = useState('');
    let user = useSelector((state) => state.user)
    let [watchItem, setWatchItem] = useState([])
    let [more , setMore] = useState(0)    

    useEffect(()=> {        
        localStorage.setItem('watChed', JSON.stringify([]))
    },[])

    useEffect(()=> {
        let numData = localStorage.getItem('watChed');
        numData = JSON.parse(numData)
        if(numData.length > 0){
            setRItem(true)
        }
    })  

    //
    let result = useQuery('', async ()=> {
        return await axios.get('https://codingapple1.github.io/userdata.json')
            .then((r)=> r.data)
            .catch((e) => console.log(e))
    })
    // 
    let result2 = useQuery('2', async ()=>{
        return await axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((r) =>{
                let copy = [...shoes, ...r.data];
                setShoes(copy);
            })
            .catch((e) => console.log(e))

    },{retry: false})
    
    function choice() {
        let choice = window.confirm(' ?')
        return choice
    }

  return (
    <div className="App">
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container>
            <Navbar.Brand className='logo'>
               <Nav.Link href="#" onClick={()=>{navigate('/')}}>StarMall</Nav.Link>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
                <Nav.Link href="#" onClick={()=>{navigate('/man')}}>Man</Nav.Link>
                <Nav.Link href="#" onClick={()=>{navigate('/woman')}}>Woman</Nav.Link>
                <Nav.Link href="#" onClick={()=>{navigate('/living')}}>Living</Nav.Link>
                <Nav.Link href="#" onClick={()=>{
                    localStorage.getItem('springStar') == null ? navigate('/notaccess')
                    : navigate('/cart')
                }}>Cart</Nav.Link>
                <Nav.Link href="#" onClick={()=>{
                    localStorage.getItem('springStar') == null ? navigate('/notaccess')
                    : navigate('/board')
                }}>CustomerBoard</Nav.Link>
                {
                    localStorage.getItem('springStar_nick') == undefined ? <Nav.Link href="#" onClick={()=>{navigate('/member')}}>membershiop</Nav.Link>
                    : null
                }
            </Nav>
                {
                    localStorage.getItem('springStar') !== null 
                    ? <Nav.Link href="#" className='user logo' style={{color: 'palevioletred', marginLeft: '10px'}} onClick={()=>{ navigate('/mypage')}}> {localStorage.getItem('springStar_nick') +' '}</Nav.Link>
                    : <Nav.Link href="#login" style={{color: 'white'}} onClick={()=>{navigate('/login')}}>login</Nav.Link>
                }
                <Nav.Link>'</Nav.Link>
                {
                    localStorage.getItem('springStar') !== null 
                    ? <Nav.Link href="#login" onClick={()=>{localStorage.removeItem('springStar');localStorage.removeItem('springStar_nick'); alert(' .');navigate('/');}}><span 
                        style={{color: 'white', marginLeft: '10px'}}>logout</span></Nav.Link>
                    : null
                }
                
            </Navbar.Collapse>
        </Container>
        </Navbar>

    {/* <Suspense fallback={<div className='mt-3' style={{color: 'grey'}}><Spinner /></div>}> */}
        <Routes>
            <Route path='/' element={
                <>
                    <div className='main-bg'>
                        <div>
                            <h1 style={{fontSize: '60px',color: 'palevioletred'}}>StarMall</h1>
                        </div>
                        {
                            rItem === true ?
                            <div className='Rview'> 
                                <p className='Rview_title' style={{fontSize: '14px', color:' white'}}>  </p>
                                <ul style={{fontSize: '12px'}}>
                                    <Rview shoes={shoes} navigate={navigate}/>
                                </ul>
                            </div> : null
                        }
                        <div className='btns'>
                            <button className='mt-3 ms-1 eventItem' onClick={()=>{navigate('/event/one')}}>1</button>
                            <button className='mt-3 ms-1 eventItem' onClick={()=>{navigate('/event/two')}}>2</button>
                            <button className='mt-3 ms-1 eventItem'>3</button>
                        </div>
                    </div>
                    
                    <div className='container'>
                        <div className='row'>
                            {
                                result2.isLoading === true ? <div className='mt-3' style={{color: 'grey'}}><Spinner /></div> :
                                shoes.map((a, i) => {
                                    return <Card shoes={shoes} i={i} key={i} fade={fade} setFade={setFade}/>
                                }) 
                            }
                        </div>
                    </div>
                    <YelloBtn className='mb-5 mt-3 YelloBtn'  onClick={()=>{
       
                        if(more == 0){
                            axios.get('https://codingapple1.github.io/shop/data3.json')
                            .then((r)=> {
                                let copy = [...shoes, ...r.data];
                                setShoes(copy);
                            })
                            .catch((e)=>{console.log(e)})
                            setMore(1)
                        } else {
                            alert('  .')
                        }
                    }}><b></b><p className='st'>(styled-component)</p></YelloBtn>                                        
                </>
                }/>
            <Route path='/detail/:id' element={<Detail className='' shoes={shoes} />}/>
            <Route path='/man' element={<Man />}/>
            <Route path='/woman' element={<Woman />}/>
            <Route path='/living' element={<Living />}/>
            <Route path='/cart' element={<Cart result={result} />} />
            <Route path='/event' element={<Event navigate={navigate} fade={fade} setFade={setFade}/>} >
                <Route path='one' element={<h5 className='mt-3 event'>   1box</h5>}/>
                <Route path='two' element={<h5 className='mt-3 event'>10%  </h5>}/>                
            </Route>
            <Route path='/member' element={<Member />} />
            <Route path='/login' element={<Login />} />
            <Route path='/board' element={<Board />} />
            <Route path='/create' element={<Create />} />
            <Route path='/mypage' element={<Mypage />} />
            <Route path='/notaccess' element={<NotAccess />} />
            <Route path='*' element={<h1 className='mt-5'><div style={{height: '600px'}} >   (404)</div></h1>}/>
        </Routes>
    {/* </Suspense> */}
        <div className='main-bg'></div>
        <div className='footer'><p className='copy'>Copyright    SpringStar Corp. All rights reserved.</p></div>
    </div>
  );
}
let YelloBtn = styled.button`
    width: 130px;
    height: 43px;
    background: powderblue;
    color: black;
    padding: 3px;
    border: 0px solid black;
    border-radius: 5px;
`
function Rview ({shoes, navigate}) {
    let numData = localStorage.getItem('watChed')
     numData = JSON.parse(numData)   //     .    
    // console.log(numData.length)
    return (
        <div>
        {         
         numData.map((a, i)=>{
            return <li key={i}><a className='rview_item' onClick={()=> navigate('/detail/'+shoes[a].id)}>{shoes[a].title}</a></li>            
         })
        }
        </div>
    )
}

function Event({navigate}) {
    let [fade2, setFade2] = useState('');
    useEffect(()=> {
        setFade2('end')
        return () => setFade2('')
    },[])
    window.scrollTo(0,0)  //  

    return (
        <div style={{height: '600px'}}>
            <div className={'box start ' +fade2}>
                <h3 className='mt-5'> </h3>
                <Outlet></Outlet>
                <button className='mt-2 event_btn' onClick={
                    ()=>{                        
                        alert('  .')
                        navigate('/')
                    }
                }></button>
            </div>
        </div>
    )
}

function Card({shoes, i, fade, setFade}) {
    
    let navigate = useNavigate();
    useEffect(()=> {
        setFade('end')
        return () => setFade('')
    },[])
        
  return (
        <div className={'col-md-4 mt-4 Card start '+fade}>
            <img onClick={()=> navigate(`/detail/${shoes[i].id}`)}
             src={"https://codingapple1.github.io/shop/shoes"+(i+1)+".jpg"} width="80%" alt="shoes"/>
            <h4>{shoes[i].title}</h4>
            <p>{shoes[i].content}</p>
        </div>
  )
}

export default App;
