import { createSlice } from '@reduxjs/toolkit'

let user = createSlice({       // useState()   (state  slice )
    name: 'user',
    initialState: { name: '', age: 20 },
    reducers: {
        changeName(state){
            return { name: '', age: 22 }
        },
        increase(state, a){
            state.age += a.payload
        }
    }
})
export let { changeName, increase } = user.actions;
export default user;