import { useEffect, useState } from 'react';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';

function Login() {

    let navigate = useNavigate()
    // window.scrollTo(0,0)  //   (     X )
    useEffect(()=>{
        window.scrollTo(0,0)  //  
    },[])

    let [user_id, setId] = useState('')
    let [password, setPw] = useState('')

    function ajax() {
        user_id.length == 0 || password.length == 0 ? alert('ID  PW   ') 
        :axios.post("https://www.springstar.shop/login2", { user_id, password }).then((res) => {
            
            if (res.data.msg == "") {
                // console.log(res.data)
                alert(` ${res.data.msg}`);
                localStorage.setItem('springStar_nick', res.data.nick);
                localStorage.setItem('springStar', res.data.jwttoken);
                navigate("/mypage");
            } else {
                alert(` ${res.data.msg}`);
            }
        });
    }

    return (
        <div className="mt-5" style={{width:'360px', height: '700px', margin:'0 auto'}}>  
        <h3 style={{color:'palevioletred', display:'inline-block'}}>StarMall</h3> 
        <FloatingLabel 
            controlId="floatingInput"
            label="Email or ID"
            className="mb-3"
        >
            <Form.Control onChange={(e)=>{setId(e.target.value)}} type="email" placeholder="name@example.com" />
        </FloatingLabel>

        <form>
        <FloatingLabel 
            controlId="floatingPassword" 
            label="Password"
            className='mb-3'
            >
            <Form.Control onChange={(e)=>{setPw(e.target.value)}} type="password" placeholder="Password" autoComplete='on' />
        </FloatingLabel>         
        
        <BlueBtn className='YelloBtn mx-1' onClick={()=> ajax()}><b></b></BlueBtn>
        <BlueBtn className='YelloBtn ms-1' onClick={()=> navigate('/')}><b></b></BlueBtn>
        </form>
    </div>
    )
}

let BlueBtn = styled.button`
    width: 100px;
    height: 40px;
    background: powderblue;
    color: black;
    padding: 3px;
    border: 0px solid black;
    border-radius: 5px;
`

export default Login;