//     API
// https://www.springstar.shop/api/articles/list 

import axios from "axios";
import { useEffect, useState } from "react";
import styled from 'styled-components';
import  Spinner  from '../component/Spinner';
import { useQueries } from "react-query";
import { useNavigate } from "react-router-dom";

function Board() {

    let navigate = useNavigate();
    let [data , setData] = useState([]);

    window.scrollTo(0,0)  //  
    
    useEffect(()=>{
        let list = axios.get('https://www.springstar.shop/api/articles/list')
        .then((r) => setData(r.data))
        .catch((e) => console.log(e))
    },[])

    useEffect(()=>{
        window.scrollTo(0,0)  //  
    },[])

    return (
        <div className="mt-5" style={{width:'90%', height: '100%', margin:'0 auto'}}>  
            <h3 style={{color:'palevioletred', display:'inline-block'}}>StarMall</h3> 
                <h1 className='mt-5'>!  ~</h1>
            <img src="https://cdn.pixabay.com/photo/2017/10/26/17/51/under-construction-2891888_960_720.jpg" alt="under construction"
            style={{margin: '0 auto', width:'350px'}}
            ></img><br/>

            <BlueBtn className='YelloBtn mx-1 mt-5' onClick={()=> navigate('/create')}><b></b></BlueBtn>
            <hr className="mt-5" style={{border: '2px solid black', maxWidth: '850px', margin: '0 auto'}}/>

            {
            data.length !== 0 ?
            <div className="row">
                <div className="col-lg-12">
                    <div className="ibox float-e-margins">
                        <div className="ibox-content">
                            <div className="table-responsive">
                                <table className="table table-hover mt-3 mb-5" style={{ 
                                    maxWidth:'800px',
                                    margin: '0 auto'

                                 }}>
                                    <thead>
                                        <tr>
                                            <th width="10%">No</th>
                                            <th width="30%"></th>
                                            {/* <th width="10%"></th> */}
                                            {/* <th width="10%"></th> */}
                                            <th width="20%">ID</th>                                            
                                        </tr>
                                    </thead>

                                    <tbody >                                    
                                    {                                      
                                        data.map((a, i) => {
                                           return  <tr key={i}>
                                                        <td>
                                                        {data[i].article_idx}
                                                        </td>
                                                        <td>
                                                        {data[i].title}
                                                        </td>
                                                        {/* <td>
                                                        {data[i].contents}
                                                        </td> */}
                                                        {/* <td>
                                                        {data[i].regist_date}
                                                        </td> */}
                                                        <td>
                                                        {data[i].regist_userid}
                                                        </td>
                                                    </tr>  
                                        })
                                    }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            : <div style={{color: 'grey'}}><Spinner /></div> }
        </div>
    )
}
let BlueBtn = styled.button`
    width: 100px;
    height: 40px;
    background: powderblue;
    color: black;
    padding: 3px;
    border: 0px solid black;
    border-radius: 5px;
`
export default Board;