import { useEffect, useState } from 'react';
import Nav from 'react-bootstrap/Nav';

function Tab({tab, setTab}) {
  
  // Tab  state  
  let [fade, setFade] = useState('');

   // UI(   )
  useEffect(()=>{   
    setTimeout(()=>{ setFade('end') }, 100)    
    return () =>{    
      setFade('')
    }
  },[tab])

  return (
    //Bootstrap Tap UI   tab state  UI  3 
    <>
      <Nav variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>setTab(0)}></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>setTab(1)}></Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={()=> setTab(2)}></Nav.Link>
        </Nav.Item>
      </Nav>
    
      { tab == 0 ? <div className={'start ' + fade}></div> : null }
      { tab == 1 ? <div className={'start ' + fade}></div> : null }
      { tab == 2 ? <div className={'start ' + fade}></div> : null }     
    </>
  );
}
export default Tab;