import { configureStore, createSlice } from '@reduxjs/toolkit'
import user from './store/user'
import cart from './store/cart'

let stock = createSlice({       
    name: 'stock',
    initialState: [10, 11, 22]
})

export default configureStore({        // state 
  reducer: {
      user: user.reducer,
     stock: stock.reducer,
      cart: cart.reducer
   }
}) 