import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import { useEffect, useState } from 'react';

function Member() {

    let navigate = useNavigate()
    // window.scrollTo(0,0)  //   (     X )

    let [user_id, setUser_id] = useState('');
    let [psword, setPsword] = useState('');
    let [psword2, setPsword2] = useState('');
    let [nickName, setNickname] = useState('');

    let member = { user_id, psword, nickName };

    useEffect(()=>{
        window.scrollTo(0,0)  //  
    },[])
        
    function ajax() {
        // console.log(member)
        psword !== psword2 ? alert('  .') 
        :axios.post("https://www.springstar.shop/memberShip", { member }).then((res) => {
            if (res.data.msg == "") {
                console.log(res.data.msg);
                alert(" .");
                navigate('/login');
            } else if (res.data.msg == "ID") {
                alert("  5  .");
            } else if (res.data.msg == "PW") {
                alert("  5  .");
            } else if (res.data.msg == "nickName") {
                alert("  2  .");
            } else {
                alert("   .");
                // document.querySelector("#user_id").focus();
            }
        });
    }

    return (
        <div className="mt-5" style={{width:'360px', height: '700px', margin:'0 auto'}}>  
            <h3 style={{color:'palevioletred', display:'inline-block'}}>StarMall</h3> 
            <FloatingLabel 
                controlId="floatingInput"
                label="Email or ID"
                className="mb-3"
            >
                <Form.Control type="email" onChange={(e)=>
                    setUser_id(e.target.value)
                } placeholder="name@example.com"/>
            </FloatingLabel>
            
            <form>
            <FloatingLabel 
                label="Password"
                className='mb-3'
                >
                <Form.Control type="password" onChange={(e)=> {
                    setPsword(e.target.value)
                }} placeholder="Password" autoComplete='on' />
            </FloatingLabel>
            </form>
            
            <form>
            <FloatingLabel 
                label="Password Check"
                className='mb-3'
                >
                <Form.Control type="password" onChange={(e)=> {
                    setPsword2(e.target.value)
                }} placeholder="Password" autoComplete='on' />
            </FloatingLabel>
            </form>

            <FloatingLabel 
                label="Nickname"                
                >
                <Form.Control type="text" onChange={(e)=> {
                    setNickname(e.target.value)
                }} placeholder="Nickname" />
            </FloatingLabel>
            
            <BlueBtn className='YelloBtn mt-3 mx-1' onClick={()=> ajax()}><b></b></BlueBtn>
            <BlueBtn className='YelloBtn ms-1' onClick={()=> navigate('/')}><b></b></BlueBtn>
        </div>
    )
}

let BlueBtn = styled.button`
    width: 100px;
    height: 40px;
    background: powderblue;
    color: black;
    padding: 3px;
    border: 0px solid black;
    border-radius: 5px;
`
export default Member;