import { useEffect, useState } from 'react';
import Table from 'react-bootstrap/Table'
import { useSelector, useDispatch } from 'react-redux'
import { addCount, clear } from '../store/cart';
import axios from 'axios';
import { useQuery } from 'react-query'
import  Spinner  from '../component/Spinner';
import { useNavigate } from 'react-router-dom';

function Cart() {
    let navigate = useNavigate();
    let dispatch = useDispatch()                     // store.js    
    let item = useSelector((state)=>  state.cart )   
    let user = useSelector((state)=>  state.user)    // Redux store 
    let [check, setCheck] = useState(false)
    let [fade, setFade] = useState('');
    useEffect(()=> {
        setFade('end')
        return () => setFade('')
    },[])

    //
    let result = useQuery('', async ()=> {
        return await axios.get('https://codingapple1.github.io/userdata.json')
            .then((r)=> r.data)
            .catch((e) => console.log(e))
    })
    window.scrollTo(0,0)  //  

    return (
        <div className={'start ' + fade} style={{height: '600px'}}>
            <h4 className='mt-5 mb-5'>{localStorage.getItem('springStar_nick') === null 
                ? 'Guest' 
                : localStorage.getItem('springStar_nick') +'  '}</h4>
            <Table responsive="md">
                <thead>
                <tr>
                    <th></th>
                    <th>ID</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>            
                </tr>
                </thead>
                <tbody>
                    {
                        item.map((a, i) => {
                           return <tr key={i}>
                                <td><input onClick={()=>{
                                    check === false ? setCheck(true) : setCheck(check)}
                                } type={'checkBox'} /></td>
                                <td>{item[i].id}</td>
                                <td>{item[i].title}</td>
                                <td>{item[i].count}</td>
                                <td>{item[i].price}</td>
                                <td>
                                    <button onClick={()=>{
                                        dispatch(addCount(item[i].id))
                                    }} >+</button>
                                </td>            
                            </tr>
                        })
                    }
                    
                </tbody>
            </Table>
                {
                    item.length === 0 ? <button className='btn btn-primary mt-3' onClick={()=> {
                        alert('  ')                        
                    }}></button> :  <button className='btn btn-primary mt-3' onClick={()=> {
                        if(check) {
                            alert(' .')
                            dispatch(clear([]))
                        } else {
                            alert('  ')
                        }
                    }}></button>
                }
               
        </div>
    )
}
export default Cart;