import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import Form from 'react-bootstrap/Form';
import { useState } from "react";
import axios from "axios";
import { useEffect } from "react";

function Create() {

    useEffect(()=>{
        window.scrollTo(0,0)  //  
    },[])

    let navigate = useNavigate();
    let [title, setTitle] = useState('');
    let [contents, setContents] = useState('');

    let article = {
        title,
        contents,
        regist_userid: 'Black Consumer'
    }

    function ajax() {
        
        title.length == 0 || contents.length == 0 ? alert('/  !')
        :axios.post("https://www.springstar.shop/api/articles/create", { article }).then((r) => {
            if(r.data.msg == '') {
                console.log(r.data)
                alert(' .')
                navigate('/board')
            }
        }).catch((e)=>console.log(e))
    }
    return (
        <div className="mt-5 mb-5" style={{height:'600px'}}>
            <h5>  </h5>
   
            <Form.Control style={{
                width:'360px', margin: '0 auto'
            }} className="mt-3 mb-2"  type="text" onChange={(e)=> {
                    setTitle(e.target.value)
            }} placeholder="" />
            
            <Form.Control style={{width:'360px', margin: '0 auto', height: '300px'}}
            as="textarea" className="mb-3" placeholder="" onChange={(e)=> {setContents(e.target.value)}}
            />
            
            <BlueBtn className='YelloBtn mx-1' onClick={(e)=> alert(' .')}><b></b></BlueBtn>
            {/* <BlueBtn className='YelloBtn mx-1' onClick={()=> ajax()}><b></b></BlueBtn> */}
            <BlueBtn className='YelloBtn ms-1' onClick={()=> navigate('/board')}><b></b></BlueBtn>
        </div>


    )
}

let BlueBtn = styled.button`
    width: 100px;
    height: 40px;
    background: powderblue;
    color: black;
    padding: 3px;
    border: 0px solid black;
    border-radius: 5px;
`

export default Create;